 /*Strip the ul of padding and list styling*/
.clear[data-v-53d81edf] {
     float: clear;
}
nav[data-v-53d81edf] {
     display: grid;
}
ul[data-v-53d81edf] {
     list-style-type:none;
     margin:0;
     padding:0;
}
 /*Create a horizontal list with spacing*/
li[data-v-53d81edf] {
     display:inline-block;
     float: center;
     margin-right: 1px;
}
 /*Style for menu links*/
li a[data-v-53d81edf] {
     display:block;
     margin-right: 50px;
     margin-left: 50px;
     height: 50px;
     text-align: center;
     line-height: 50px;
     color: blue;
     background: #ffe6ff;
     width: 150px;
     position: relative;
     font-family: 'Gill Sans';
     font-size: 18px;
}
 /*Active color*/
li a.active[data-v-53d81edf] {
     color: red;
     background: blue;
}
 /*Hover state for top level links*/
li:hover a[data-v-53d81edf] {
     font-size: 20px;
     color: red;
}
 /*Style 'show menu' label button and hide it by default*/
.show-menu[data-v-53d81edf] {
     text-decoration: none;
     color: #fff;
     background: #FF5035;
     text-align: center;
     padding: 10px 0;
     display: none;
}
 /*Hide checkbox*/
input[type=checkbox][data-v-53d81edf]{
     display: none;
}
 /*Show menu when invisible checkbox is checked*/
input[type=checkbox]:checked ~ #menu[data-v-53d81edf]{
     display: block;
}
